@import "./components/header";
@import "zeroing";
@import "important";
@import "font-face";
@import "mixins";
.container {
    width: 1170px;
    margin: 0 auto;
}
.header .burger__menu._active .burger__container .burger__navigation .link__four {
    color: #FEAB47;
}
.header .header__section .header__navigation .link__four {
    color: #FEAB47;
}

.main {
    padding: 20px 0px 80px 0px;
}
h1 {
    text-align: center;
}
.section__news {
    display: grid;
    grid-template-columns: 2fr 1fr;
}
.container__news {
    display: flex;
    flex-direction: column;
    align-items: start;
    .line {
        width: 48.5px;
        border: 2px solid #000000;
        border-radius: 3px;
        margin: 40px 0px
    }
    .line:hover {
        box-shadow: 0px 1px 4px #7000FF;
        transition: all 0.4s ease;
    }
    .text {
        padding-top: 20px;
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        p {
            font-family: 'Nunito';
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            color: #000000;
        }
        p:nth-child(2) {
            padding: 25px 0px;
            a {
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 600;
                font-size: 16px;
                line-height: 22px;
                text-decoration-line: underline;
                color: #075DDD;
            }
        }
    }
}
.block__news {
    padding-left: 30px;
    .container__news {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-top: 20px;
        span {
            font-family: 'Nunito';
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 19px;
            color: #000000;
            padding-bottom: 25px;
        }
        .news__company {
            display: flex;
            flex-direction: column;
            align-items: start;
            .card__new {
              position: relative;
              width: 370px;
              height: 300px;
              background: #ffffff;
              box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
              margin-bottom: 30px;
              img {
                position: absolute;
                left: 20px;
                top: 20px;
              }
              p {
                position: absolute;
                width: 330px;
                height: 80px;
                left: 20px;
                top: 160px;
                font-family: "Nunito";
                font-style: normal;
                font-weight: 600;
                font-size: 15px;
                line-height: 20px;
                color: #000000;
              }
              .line {
                position: absolute;
                left: 280px;
                top: 240px;
                width: 74.5px;
                border: 2px solid #FEAB47;
                border-radius: 5px;
              }
            }
            .card__new:hover {
              cursor: pointer;
              box-shadow: -1px 0px 5px 1px rgba(0, 0, 0, 0.25);
              transition: all 600ms;
            }
            .card__news-two {
              display: flex;
              flex-direction: column;
              padding: 0px 0px;
              .card__new {
                position: relative;
                width: 370px;
                height: 160px;
                background: #ffffff;
                box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
                border-radius: 0px 0px 0px 0px;
                margin-bottom: 30px;
                p {
                  position: absolute;
                  width: 330px;
                  height: 80px;
                  left: 20px;
                  top: 20px;
                  font-family: "Nunito";
                  font-style: normal;
                  font-weight: 400;
                  font-size: 15px;
                  line-height: 20px;
                  color: #000000;
                }
                .line {
                    position: absolute;
                    left: 270px;
                    top: 100px;
                    width: 74.5px;
                    border: 2px solid #FEAB47;
                    border-radius: 5px;
                  }
              }
              .card__new:hover {
                cursor: pointer;
                box-shadow: -1px 0px 5px 1px rgba(0, 0, 0, 0.25);
                transition: all 600ms;
              }
            }
            .card__new-big {
              position: relative;
              width: 370px;
              height: 300px;
              background: #ffffff;
              box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
              img {
                position: absolute;
                left: 20px;
                top: 20px;
              }
              p {
                position: absolute;
                width: 330px;
                height: 80px;
                left: 20px;
                top: 160px;
                font-family: "Nunito";
                font-style: normal;
                font-weight: 400;
                font-size: 15px;
                line-height: 20px;
                color: #000000;
              }
              .line {
                position: absolute;
                left: 280px;
                top: 240px;
                width: 74.5px;
                border: 2px solid #FEAB47;
                border-radius: 5px;
              }
            }
            .card__new-big:hover {
              cursor: pointer;
              box-shadow: -1px 0px 5px 1px rgba(0, 0, 0, 0.25);
              transition: all 600ms;
            }
        }
        
    }
}


@import "./components/footer";
@import "./breakpoints/breakpoint-novetly";
.footer .footer__container .footer__section .footer__navigation .link__four {
    color: #FEAB47;
}
.footer .footer__container-phone .footer__section .footer__section-left .footer__navigation .link__four {
    color: #FEAB47;
}