.footer__container-phone {
   display: none;
}

.footer {
   width: 100%;
   height: 559px;
   background: #191919;
   padding: 120px 0px;
   .footer__container {
      .block__img {
         display: flex;
         align-items: center;
         justify-content: center;
         flex-direction: column;
         padding-bottom: 60px;
         p {
            font-family: 'Ubuntu';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 18px;
            color: #FFFFFF;
         }
         .img__a {
            width: 148px;
            padding-top: 20px;
            display: flex;
            justify-content: space-between;
            a:nth-child(3) {
               padding-top: 2px;
            }
         }
      }
      hr {
         border: 1px solid #fff;
      }
      .footer__section {
         margin-top: 55px;
         display: flex;
         align-items: center;
         justify-content: space-between;
         padding: 11px 0px;
         .logo {
            display: flex;
            align-items: center;
            justify-content: space-between;
            img {
               padding-right: 10px;
            }
            p {
               font-family: 'Nunito';
               font-style: normal;
               font-weight: 600;
               font-size: 16px;
               line-height: 22px;
               color: #FFFFFF;
            }
         }
         .footer__navigation {
            display: flex;
            align-items: center;
            .link {
               font-family: 'Nunito';
               font-style: normal;
               font-weight: 400;
               font-size: 16px;
               line-height: 18px;
               color: #FFFFFF;
               padding: 0px 30px;
            }
         }
   
      }
      .footer__social {
         padding-top: 55px;
         display: flex;
         align-items: center;
         justify-content: space-between;
         p {
            font-family: 'Nunito';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 18px;
            color: rgba(255, 255, 255, 0.6);
         }
         .social__network {
            width: 126px;
            display: flex;
            justify-content: space-between;
         }
      }
   }
}
@media screen and (max-width: 1060px) {
   .footer {
      width: 100%;
      height: 489px;
      background: #191919;
      padding: 80px 0px;
   }
   .footer .footer__container .footer__section .footer__navigation .link {
      font-family: "Nunito";
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 18px;
      padding: 0px 15px;
   }
}
@media screen and(max-width: 800px) {
   .footer {
      height: 559px;
      padding: 60px 0px;

      .footer__container {
         display: none;
      }

      .footer__container-phone {
         display: block;

         .block__img {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            padding-bottom: 60px;

            p {
               font-family: 'Nunito';
               font-style: normal;
               font-weight: 400;
               font-size: 16px;
               line-height: 18px;
               color: #FFFFFF;
            }

            .img__a {
               width: 148px;
               padding-top: 20px;
               display: flex;
               justify-content: space-between;

               a:nth-child(3) {
                  padding-top: 2px;
               }
            }
         }

         hr {
            border: 1px solid #fff;
         }

         .footer__section {
            margin-top: 30px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            .footer__section-left {
               .logo {
                  width: 110px;
                  display: flex;
                  align-items: center;
                  justify-content: space-between;

                  img {
                     padding-right: 10px;
                  }

                  p {
                     font-family: 'Nunito';
                     font-style: normal;
                     font-weight: 600;
                     font-size: 16px;
                     line-height: 22px;
                     color: #FFFFFF;
                  }
               }

               .footer__navigation {
                  padding-top: 20px;
                  display: flex;
                  align-items: start;
                  flex-direction: column;

                  .link {
                     font-family: 'Nunito';
                     font-style: normal;
                     font-weight: 400;
                     font-size: 16px;
                     line-height: 18px;
                     color: #fff;
                     padding: 8px 0px;
                  }
               }
            }

            .footer__section-right {
               display: flex;
               flex-direction: column;
               align-items: flex-end;

               .btn {}

               p {
                  font-family: 'Nunito';
                  font-style: normal;
                  font-weight: 400;
                  font-size: 16px;
                  line-height: 18px;
                  color: rgba(255, 255, 255, 0.6);
                  padding: 75px 0px;
               }

               .social__network {
                  img {
                     margin-left: 10px;
                  }
               }
            }
         }
      }
   }
}
@media screen and (max-width: 576px) {
   .footer {
      height: 459px;
      padding: 40px 0px;
   }
   .footer .footer__container-phone .footer__section .footer__section-right p {
      font-family: "Nunito";
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 18px;
      color: rgba(255, 255, 255, 0.6);
      padding: 65px 0px;
      text-align: right;
   }
   .footer .footer__container-phone .footer__section .footer__section-right {
      .btn {
         padding: 11px 30px;
      }
   }
   .footer .footer__container-phone .block__img {
      padding-bottom: 20px;
   }
   .footer .footer__container-phone .footer__section {
      margin-top: 20px;
   }
}